.section {
  display: flex;
  z-index: 2;
  margin-bottom: 0;
  height: 550px;
  width: auto;
  position: absolute;
  background-color: #dfe08e;
}

.detail {
  flex-direction: row;
  margin: auto 5em;
}

.title {
  font-size: 1.5em;
  font-weight: 800;
  margin: 3px auto;
}

.dishTypes {
  font-size: 1.2em;
  font-weight: 700;
  margin: 3px auto;
}

.instructions {
  margin: 1em auto;
  font-size: 0.9em;
}

@media screen and (max-width: 769px) {
  .section {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    min-height: 100vh;
    position: fixed;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%);
  }
  .detail {
    margin: auto 2em;
  }
  .instructions {
    display: none;
  }
}
